html {
  font-size: 62.5%; }

body {
  padding: 0;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

* {
  box-sizing: border-box; }

#root {
  display: flex;
  flex-direction: column;
  min-height: 100vh; }



.search {
  flex-grow: 1;
  display: flex;
  justify-content: center;
  position: relative;
  height: auto; }
  .search .search-recommend {
    overflow-y: auto;
    max-height: 30rem;
    margin-top: 3.2rem;
    width: 50rem;
    position: absolute;
    z-index: 1;
    background-color: #fff;
    border: 1px solid #d9d9d9; }

.search-item {
  display: block;
  padding: 0.5rem 1rem; }
  .search-item:hover {
    background-color: #d5f0ff; }

.ant-menu .avatar {
  display: flex;
  align-items: center; }
  .ant-menu .avatar:hover {
    border-color: #ffffff; }
  .ant-menu .avatar > img {
    border-radius: 50%;
    width: 3rem;
    height: 3rem; }

.loading .ant-skeleton {
  display: flex;
  justify-content: center;
  align-items: center; }
  .loading .ant-skeleton .ant-skeleton-content {
    display: flex;
    align-items: center;
    margin-top: -0.8rem !important; }
    .loading .ant-skeleton .ant-skeleton-content .ant-skeleton-title {
      width: 100px !important; }

.loading::before {
  content: none !important; }

.sign-in {
  display: flex;
  justify-content: center; }
  .sign-in .sign-in-form {
    max-width: 300px; }
  .sign-in .sign-in-form-forgot {
    float: right; }
  .sign-in .sign-in-form-button {
    width: 100%; }

.ant-modal-content {
  margin: 0 auto; }

.sign-up {
  flex-grow: 1;
  display: flex;
  justify-content: center;
  align-items: center; }
  .sign-up > .ant-form {
    padding: 0rem 4rem 0 0; }
  .sign-up .ant-form-item {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-auto-flow: column; }
    .sign-up .ant-form-item::before {
      content: none; }
    .sign-up .ant-form-item::after {
      content: none; }
    .sign-up .ant-form-item .ant-form-item-label {
      margin-right: 2rem;
      grid-column: 1/2;
      width: 100%; }
    .sign-up .ant-form-item .ant-form-item-control-wrapper {
      width: 100%;
      grid-column: 2/4;
      margin-left: 0;
      text-align: center; }
    .sign-up .ant-form-item .ant-btn-primary {
      width: 100%; }

@media (max-width: 48em) {
  .sign-up {
    padding: 0 1rem; }
    .sign-up .ant-form-item .ant-form-item-label {
      font-size: 0.5rem; } }

.menuBar {
  padding: 0 20px;
  border-bottom: solid 1px #e8e8e8;
  box-shadow: 0 0 30px #f3f1f1; }

.logo {
  width: 200px;
  float: left; }

.logo a {
  display: inline-block;
  font-size: 20px;
  padding: 19px 20px;
  text-transform: capitalize;
  display: flex;
  justify-content: flex-end; }

.menuCon {
  width: calc(100% - 200px);
  float: left;
  display: flex; }

.menuCon .ant-menu-item {
  padding: 0px 5px; }

.menuCon .ant-menu-submenu-title {
  padding: 10px 20px; }

.menuCon .ant-menu-item a,
.menuCon .ant-menu-submenu-title a {
  padding: 10px 15px; }

.menuCon .ant-menu-horizontal {
  border-bottom: none; }

.menuCon .leftMenu {
  float: left;
  flex-grow: 1;
  display: flex;
  align-items: center;
  justify-content: center; }
  .menuCon .leftMenu .ant-input-group-wrapper {
    max-width: 500px; }

.menuCon .rightMenu {
  float: right; }

.barsMenu {
  float: right;
  height: 32px;
  padding: 6px;
  margin-top: 8px;
  display: none;
  background: none; }

.barsBtn {
  display: block;
  width: 20px;
  height: 2px;
  background: #1890ff;
  position: relative; }

.barsBtn:after,
.barsBtn:before {
  content: attr(x);
  width: 20px;
  position: absolute;
  top: -6px;
  left: 0;
  height: 2px;
  background: #1890ff; }

.barsBtn:after {
  top: auto;
  bottom: -6px; }

.ant-drawer-body {
  padding: 0; }

.ant-drawer-body .ant-menu-horizontal > .ant-menu-item,
.ant-drawer-body .ant-menu-horizontal > .ant-menu-submenu {
  display: inline-block;
  width: 100%; }

.ant-drawer-body .ant-menu-horizontal {
  border-bottom: none; }

.ant-drawer-body .ant-menu-horizontal > .ant-menu-item:hover {
  border-bottom-color: transparent; }

@media (max-width: 48em) {
  .menuBar {
    display: flex; }
  .menuCon {
    justify-content: flex-end;
    flex: 1 1; }
    .menuCon .leftMenu,
    .menuCon .rightMenu {
      display: none; }
    .menuCon .ant-menu-item,
    .menuCon .ant-menu-submenu-title {
      padding: 1px 20px; }
    .menuCon .barsMenu {
      display: inline-block;
      margin: 14px 0; }
  .logo {
    width: 70px;
    margin: 4px 0; }
    .logo a {
      margin-left: -20px;
      padding: 10px 0px 10px 20px; }
  .ant-drawer-header {
    text-align: center; }
  .ant-drawer-body {
    text-align: center; }
    .ant-drawer-body .ant-input-group-wrapper {
      padding: 15px;
      max-width: 90%; } }

.footer {
  height: 7rem;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f7f3f3; }

.loading {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-grow: 1; }
  .loading .atom-spinner {
    width: 20rem;
    height: 20rem; }

